h1 { font-size: 36px; }

h5 { font-family: "museo500", Arial, sans-serif; font-size: 24px; }

h6 { font-family: "museo300", Arial, sans-serif; font-size: 18px; }

body { font-family: "museo300", Arial, sans-serif; font-size: 16px; line-height: 1.4; color: #000; }

a { color: #000; text-decoration: none; }

.pink { color: #da295b; }

.green { color: #227e75; }

.btn { font-family: "museo500", Arial, sans-serif; display: inline-block; text-transform: uppercase; cursor: pointer; height: 45px; line-height: 45px; font-size: 18px; padding: 0 35px; margin: 20px 0; }
.btn_pink { background-color: #da295b; color: #fff; }
.btn_white { background-color: #fff; color: #da295b; }
.btn_big { padding: 0 50px; font-size: 24px; }

.line { background: url("../img/dot.png") repeat; width: 920px; height: 2px; margin: 75px auto; }

.ico { display: inline-block; vertical-align: middle; }
.ico_tw { background: url("../img/social_media_sprite.svg"); background-position: 0 0; background-size: cover; width: 33px; height: 33px; }
.ico_tw-small { background: url("../img/social_media_sprite.svg"); background-position: 0 0; background-size: cover; width: 18px; height: 18px; }
.ico_fb { background: url("../img/social_media_sprite.svg"); background-position: 84px 0; background-size: cover; width: 33px; height: 33px; }
.ico_fb-small { background: url("../img/social_media_sprite.svg"); background-position: 45px 0; background-size: cover; width: 18px; height: 18px; }
.ico_vk { background: url("../img/social_media_sprite.svg"); background-position: 33px 0; background-size: cover; width: 33px; height: 33px; }
.ico_vk-small { background: url("../img/social_media_sprite.svg"); background-position: 18px 0; background-size: cover; width: 18px; height: 18px; }

.row { display: flex; flex-direction: row; justify-content: space-between; }

.container { min-width: 1200px; width: 100%; max-width: 1200px; padding: 0 15px; margin: 0 auto; box-sizing: border-box; }

.user-menu__list { display: flex; }
.user-menu__el { margin-right: 50px; }
.user-menu__link { display: block; font-size: 20px; }

.lang { text-transform: uppercase; font-size: 18px; }
.lang__list { display: flex; }
.lang__el { margin-left: 34px; }

.title { font-family: "museo700", Arial, sans-serif; font-size: 110px; background-color: #fff; }

.descr { font-family: "museo100", Arial, sans-serif; font-size: 20px; }
.descr p { margin-bottom: 20px; }

.social_small .social__list { justify-content: center; }
.social_small .social__el { margin-left: 9px; }
.social_small .social__el:first-child { margin-left: 0; }
.social__list { display: flex; }
.social__el { margin-left: 15px; }
.social__link { display: block; }

.days { display: flex; justify-content: space-between; }

.day { width: 20%; }
.day .vertical-list__item { margin-left: 20px; margin-top: 20px; }
.day h5 { color: #227e75; margin-bottom: 20px; }
.day:last-child h5 { color: #da295b; }
.day:last-child p { margin: 0; }
.day:last-child .vertical-list__item { margin: 0; }
.day:last-child .vertical-list__el { margin-bottom: 20px; }
.day p { font-family: "museo100", Arial, sans-serif; margin: 35px 0; }

.ital { font-family: "museo500ital", Arial, sans-serif; }

.schedule { display: flex; justify-content: space-between; }
.schedule .col { width: 30%; }
.schedule h5 { font-family: "museo300", Arial, sans-serif; background-color: #ebebeb; padding: 10px 15px; margin-bottom: 45px; }
.schedule .vertical-list__el { font-size: 18px; margin-bottom: 50px; }

.time { font-family: "museo500", Arial, sans-serif; }

.h100 { font-family: "museo100", Arial, sans-serif; margin-top: 25px; }

.sale { padding: 60px 0 80px; margin: 0 auto; width: 900px; color: #fff; font-size: 30px; text-align: center; background-color: #107063; }
.sale__top { text-transform: uppercase; margin-bottom: 40px; }
.sale__center { font-family: "museo100", Arial, sans-serif; margin-bottom: 25px; }

.item { width: 220px; text-align: center; color: #fff; }
.item_1 { background: url("../img/speaker-1.jpg") center; background-size: cover; }
.item_2 { background: url("../img/speaker-2.jpg") center; background-size: cover; }
.item_3 { background: url("../img/speaker-3.jpg") center; background-size: cover; }
.item_4 { background: url("../img/speaker-4.jpg") center; background-size: cover; }
.item_5 { background: url("../img/speaker-5.jpg") center; background-size: cover; }
.item_6 { background: url("../img/speaker-6.jpg") center; background-size: cover; }
.item__top { padding: 155px 0; }
.item__bottom { padding: 9px 0; background-color: rgba(0, 0, 0, 0.7); }
.item__bottom p { opacity: 0.85; }
.item__descr { font-family: "museo100", Arial, sans-serif; font-size: 13px; }

.horizontal-list__item { display: flex; flex-direction: row; justify-content: center; }
.horizontal-list__el { margin: 0 5px; }

.circle { width: 200px; height: 200px; background-color: #959595; border-radius: 50%; }

.section.section_about { padding-bottom: 0; }

.section-partners .row { flex-wrap: wrap; }
.section-partners .row:first-child { justify-content: center; }

.partner { margin: 0 15px 50px; width: 200px; text-align: center; font-size: 24px; }
.partner p { padding-top: 20px; }

.map { width: 100%; }

.mb_YTPTime { display: none; }

.ytpicon { display: none; }

.data-choice { font-family: "museo300", Arial, sans-serif; font-size: 25px; margin: 60px 0 40px; }
.data-choice__list { display: inline-flex; justify-content: center; align-items: center; border: 1px solid #ebebeb; border-radius: 40px; }
.data-choice__el { padding: 15px 45px; cursor: pointer; border-radius: 40px; }
.data-choice .is-active { background-color: #107063; color: #fff; }
.data-choice span { font-family: "museo300ital", Arial, sans-serif; }

.top-line { padding: 10px 0; display: flex; flex-direction: row; justify-content: space-between; align-items: center; }

.header { margin-bottom: 100px; }
.header__bottom { background-size: cover; padding: 90px 0 20px; }

.section { padding-bottom: 150px; }
.section__top { text-align: center; margin-bottom: 40px; }
.section__title { color: #107063; }
.section__descr { font-size: 24px; }

.footer { font-family: "museo500", Arial, sans-serif; font-size: 24px; }
.footer__top { text-align: center; }
.footer__bottom { display: flex; justify-content: space-between; align-items: center; margin: 25px 0; }
.footer__left p { margin-top: 10px; }
.footer .section__title { margin-bottom: 60px; }


